  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  mix-blend-mode: difference; }
  .c-cursor:before {
    background-color: #ffffff;
    border-radius: 50%;
    height: 100%;
    content: "";
    width: 100%;
    position: absolute;
    left: 0;
    top: 0;
    mix-blend-mode: difference; }
    .is-hover .c-cursor:before {
